import React, { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const SummaryPage = ({ resumeData, handleInputChange, onNext, onPrev }) => {
    const [showAIPanel, setShowAIPanel] = useState(false);
    const [aiPrompt, setAiPrompt] = useState('');
    const [isGenerating, setIsGenerating] = useState(false);
    const [jobUrl, setJobUrl] = useState('');
    const [isAnalyzingJob, setIsAnalyzingJob] = useState(false);
    const [keywordMatches, setKeywordMatches] = useState([]);
    const [missingKeywords, setMissingKeywords] = useState([]);
    const [suggestions, setSuggestions] = useState([]);
    const [aiTone, setAiTone] = useState('professional');
    const [activeTab, setActiveTab] = useState('write');
    const [typingProgress, setTypingProgress] = useState(0);
    const textareaRef = useRef(null);

    // Animation variants
    const containerVariants = {
        hidden: { opacity: 0 },
        visible: {
            opacity: 1,
            transition: {
                duration: 0.6,
                staggerChildren: 0.1
            }
        }
    };

    const itemVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: {
            opacity: 1,
            y: 0,
            transition: {
                duration: 0.5,
                ease: "easeOut"
            }
        }
    };

    // Simulate typing animation for AI generation
    useEffect(() => {
        if (isGenerating) {
            const interval = setInterval(() => {
                setTypingProgress(prev => {
                    if (prev >= 100) {
                        clearInterval(interval);
                        return 100;
                    }
                    return prev + 10;
                });
            }, 200);
            return () => clearInterval(interval);
        } else {
            setTypingProgress(0);
        }
    }, [isGenerating]);

    // OpenAI API integration for AI summary generation
    const generateAISummary = async () => {
        if (!resumeData.professionalSummary.trim()) {
            toast.error('Please write some content first for AI to enhance');
            return;
        }

        setIsGenerating(true);
        setTypingProgress(0);

        try {
            // Replace with your actual OpenAI API call
            const response = await callOpenAISummaryAPI(resumeData.professionalSummary, aiTone);

            setSuggestions(response.suggestions);

            handleInputChange('professionalSummary', '', response.enhancedSummary);

        } catch (error) {
            console.error('AI generation failed:', error);
            // Fallback to local suggestions
            setSuggestions([
                "Add quantifiable achievements and metrics",
                "Include specific skills and technologies mentioned",
                "Highlight leadership and collaboration experiences",
                "Focus on results and impact rather than just responsibilities",
                "Use action verbs to start each bullet point",
                "Tailor to the specific industry or role you're targeting"
            ]);
        } finally {
            setIsGenerating(false);
        }
    };

    // OpenAI API integration for job description analysis
    const analyzeJobDescription = async () => {
        if (!jobUrl.trim()) return;

        setIsAnalyzingJob(true);

        try {
            // Replace with your actual OpenAI API call for job analysis
            const analysis = await callOpenAIJobAnalysisAPI(jobUrl);

            setKeywordMatches(analysis.matchedKeywords);
            setMissingKeywords(analysis.missingKeywords);

        } catch (error) {
            console.error('Job analysis failed:', error);
            // Fallback analysis
            const mockKeywords = ['management', 'leadership', 'strategy', 'analysis'];
            setKeywordMatches(mockKeywords.slice(0, 2));
            setMissingKeywords(mockKeywords.slice(2));
        } finally {
            setIsAnalyzingJob(false);
        }
    };

    // Mock OpenAI API calls (replace with actual API implementation)
    const callOpenAISummaryAPI = async (currentSummary, tone) => {
        // Simulate API call delay
        await new Promise(resolve => setTimeout(resolve, 2000));

        return {
            enhancedSummary: `${currentSummary}\n\nEnhanced with AI: Added quantifiable results, optimized for ${tone} tone, and improved impact statements.`,
            suggestions: [
                "Add specific metrics to quantify your achievements",
                "Include relevant technical skills and certifications",
                "Highlight leadership and team collaboration experiences",
                "Focus on business impact and results",
                "Use industry-specific keywords for better ATS optimization",
                "Structure with clear, scannable bullet points"
            ]
        };
    };

    const callOpenAIJobAnalysisAPI = async (url) => {
        // Simulate API call delay
        await new Promise(resolve => setTimeout(resolve, 1500));

        // Mock analysis based on URL
        const skills = ['JavaScript', 'React', 'Node.js', 'Python', 'AWS', 'Agile', 'Leadership'];
        const matched = skills.slice(0, 3);
        const missing = skills.slice(3);

        return {
            matchedKeywords: matched,
            missingKeywords: missing,
            primarySkills: ['software development', 'web technologies', 'cloud computing'],
            jobTitle: 'Senior Software Engineer',
            company: 'Tech Company Inc.'
        };
    };

    const applySuggestion = (suggestion) => {
        const currentSummary = resumeData.professionalSummary;
        const newSummary = currentSummary ? `${currentSummary}\n\n${suggestion}` : suggestion;
        handleInputChange('professionalSummary', '', newSummary);
        focusTextarea();
    };

    const calculateScore = () => {
        if (!keywordMatches.length) return 0;
        const totalKeywords = keywordMatches.length + missingKeywords.length;
        return Math.round((keywordMatches.length / totalKeywords) * 100);
    };

    const focusTextarea = () => {
        textareaRef.current?.focus();
    };

    const clearAnalysis = () => {
        setJobUrl('');
        setKeywordMatches([]);
        setMissingKeywords([]);
    };

    const getWordCount = () => {
        return resumeData.professionalSummary.split(/\s+/).filter(word => word.length > 0).length;
    };

    const getSentenceCount = () => {
        return resumeData.professionalSummary.split(/[.!?]+/).filter(sentence => sentence.trim().length > 0).length;
    };

    return (
        <motion.div
            style={styles.container}
            variants={containerVariants}
            initial="hidden"
            animate="visible"
        >
            <motion.div style={styles.header} variants={itemVariants}>
                <h1 style={styles.title}>üìù Professional Summary</h1>
                <p style={styles.subtitle}>Craft a compelling summary with AI enhancement and job description matching</p>
            </motion.div>

            {/* Tab Navigation */}
            <motion.div style={styles.tabContainer} variants={itemVariants}>
                <div style={styles.tabs}>
                    <button
                        style={{
                            ...styles.tab,
                            ...(activeTab === 'write' && styles.tabActive)
                        }}
                        onClick={() => setActiveTab('write')}
                    >
                        ‚úçÔ∏è Write Summary
                    </button>
                    <button
                        style={{
                            ...styles.tab,
                            ...(activeTab === 'analyze' && styles.tabActive)
                        }}
                        onClick={() => setActiveTab('analyze')}
                    >
                        üéØ Job Match Analysis
                    </button>
                </div>
            </motion.div>

            <div style={styles.mainLayout}>
                {/* Left Column - Writing Area */}
                <motion.div style={styles.writingColumn} variants={itemVariants}>
                    <AnimatePresence mode="wait">
                        {activeTab === 'write' && (
                            <motion.div
                                key="write"
                                style={styles.formGroup}
                                initial={{ opacity: 0, x: -20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: 20 }}
                                transition={{ duration: 0.3 }}
                            >
                                <div style={styles.labelRow}>
                                    <label style={styles.label}>Professional Summary *</label>
                                    <div style={styles.aiActions}>
                                        <motion.button
                                            onClick={generateAISummary}
                                            disabled={isGenerating || !resumeData.professionalSummary.trim()}
                                            style={{
                                                ...styles.generateButton,
                                                ...((isGenerating || !resumeData.professionalSummary.trim()) && styles.generateButtonDisabled)
                                            }}
                                            whileHover={{ scale: 1.02 }}
                                            whileTap={{ scale: 0.98 }}
                                        >
                                            {isGenerating ? (
                                                <>
                                                    <motion.span
                                                        style={styles.spinner}
                                                        animate={{ rotate: 360 }}
                                                        transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                                                    ></motion.span>
                                                    AI Enhancing... {typingProgress}%
                                                </>
                                            ) : (
                                                'ü™Ñ Enhance with OpenAI'
                                            )}
                                        </motion.button>
                                    </div>
                                </div>

                                <motion.textarea
                                    ref={textareaRef}
                                    value={resumeData.professionalSummary}
                                    onChange={(e) => handleInputChange('professionalSummary', '', e.target.value)}
                                    style={{ ...styles.input, ...styles.textarea }}
                                    rows={16}
                                    placeholder="Describe your professional background, skills, achievements, and career objectives. Focus on quantifiable results and relevant experience..."
                                    whileFocus={{
                                        boxShadow: "0 0 0 3px rgba(59, 130, 246, 0.1)",
                                        borderColor: "#3b82f6"
                                    }}
                                />

                                <div style={styles.charCount}>
                                    {resumeData.professionalSummary.length} characters ‚Ä¢
                                    {getWordCount()} words ‚Ä¢
                                    {getSentenceCount()} sentences
                                </div>

                                {/* Progress Indicators */}
                                <div style={styles.progressIndicators}>
                                    <div style={styles.progressItem}>
                                        <span style={styles.progressLabel}>Completeness</span>
                                        <div style={styles.progressBar}>
                                            <motion.div
                                                style={{
                                                    ...styles.progressFill,
                                                    width: `${Math.min((resumeData.professionalSummary.length / 500) * 100, 100)}%`,
                                                    backgroundColor: resumeData.professionalSummary.length >= 300 ? '#10b981' : '#f59e0b'
                                                }}
                                                initial={{ width: 0 }}
                                                animate={{ width: `${Math.min((resumeData.professionalSummary.length / 500) * 100, 100)}%` }}
                                                transition={{ duration: 0.8, ease: "easeOut" }}
                                            />
                                        </div>
                                        <span style={styles.progressValue}>
                                            {Math.min((resumeData.professionalSummary.length / 500) * 100, 100).toFixed(0)}%
                                        </span>
                                    </div>
                                </div>

                                {/* AI Suggestions */}
                                <AnimatePresence>
                                    {suggestions.length > 0 && (
                                        <motion.div
                                            style={styles.suggestionsBox}
                                            initial={{ opacity: 0, y: 20 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            exit={{ opacity: 0, y: -20 }}
                                        >
                                            <h4 style={styles.suggestionsTitle}>üí° AI Writing Suggestions</h4>
                                            <p style={styles.suggestionsSubtitle}>
                                                Based on your summary, OpenAI suggests these improvements:
                                            </p>
                                            <div style={styles.suggestionList}>
                                                {suggestions.map((suggestion, index) => (
                                                    <motion.div
                                                        key={index}
                                                        style={styles.suggestionItem}
                                                        onClick={() => applySuggestion(suggestion)}
                                                        title="Click to add to summary"
                                                        initial={{ opacity: 0, x: -20 }}
                                                        animate={{ opacity: 1, x: 0 }}
                                                        transition={{ delay: index * 0.1 }}
                                                        whileHover={{ scale: 1.02, backgroundColor: '#f8fafc' }}
                                                        whileTap={{ scale: 0.98 }}
                                                    >
                                                        <span style={styles.suggestionIcon}>üí°</span>
                                                        {suggestion}
                                                    </motion.div>
                                                ))}
                                            </div>
                                        </motion.div>
                                    )}
                                </AnimatePresence>
                            </motion.div>
                        )}

                        {activeTab === 'analyze' && (
                            <motion.div
                                key="analyze"
                                style={styles.jobAnalysis}
                                initial={{ opacity: 0, x: -20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: 20 }}
                                transition={{ duration: 0.3 }}
                            >
                                <h4 style={styles.sectionTitle}>üéØ Analyze Job Description</h4>
                                <div style={styles.urlInputGroup}>
                                    <input
                                        type="url"
                                        value={jobUrl}
                                        onChange={(e) => setJobUrl(e.target.value)}
                                        placeholder="Paste job description URL (LinkedIn, Indeed, etc.)"
                                        style={styles.urlInput}
                                    />
                                    <div style={styles.urlActions}>
                                        <button
                                            onClick={analyzeJobDescription}
                                            disabled={isAnalyzingJob || !jobUrl.trim()}
                                            style={{
                                                ...styles.analyzeButton,
                                                ...((isAnalyzingJob || !jobUrl.trim()) && styles.analyzeButtonDisabled)
                                            }}
                                        >
                                            {isAnalyzingJob ? (
                                                <>
                                                    <motion.span
                                                        style={styles.spinner}
                                                        animate={{ rotate: 360 }}
                                                        transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                                                    ></motion.span>
                                                    Analyzing...
                                                </>
                                            ) : (
                                                'Analyze with AI'
                                            )}
                                        </button>
                                        {jobUrl && (
                                            <button
                                                onClick={clearAnalysis}
                                                style={styles.clearButton}
                                            >
                                                Clear
                                            </button>
                                        )}
                                    </div>
                                </div>

                                <AnimatePresence>
                                    {(keywordMatches.length > 0 || missingKeywords.length > 0) && (
                                        <motion.div
                                            style={styles.analysisResults}
                                            initial={{ opacity: 0, height: 0 }}
                                            animate={{ opacity: 1, height: 'auto' }}
                                            exit={{ opacity: 0, height: 0 }}
                                        >
                                            <div style={styles.scoreCard}>
                                                <div style={styles.score}>
                                                    <span>AI Match Score: {calculateScore()}%</span>
                                                    <span style={styles.scoreInfo}>
                                                        {calculateScore() >= 80 ? 'Excellent match!' :
                                                            calculateScore() >= 60 ? 'Good match' :
                                                                'Needs improvement'}
                                                    </span>
                                                </div>
                                                <div style={styles.scoreBar}>
                                                    <motion.div
                                                        style={{
                                                            ...styles.scoreFill,
                                                            width: `${calculateScore()}%`,
                                                            backgroundColor: calculateScore() >= 80 ? '#10b981' :
                                                                calculateScore() >= 60 ? '#f59e0b' : '#ef4444'
                                                        }}
                                                        initial={{ width: 0 }}
                                                        animate={{ width: `${calculateScore()}%` }}
                                                        transition={{ duration: 1, ease: "easeOut" }}
                                                    ></motion.div>
                                                </div>
                                            </div>

                                            <div style={styles.keywordSection}>
                                                <motion.div style={styles.keywordGroup} variants={itemVariants}>
                                                    <span style={styles.keywordLabel}>
                                                        ‚úÖ AI-Matched Keywords ({keywordMatches.length})
                                                    </span>
                                                    <div style={styles.keywordList}>
                                                        {keywordMatches.map((keyword, index) => (
                                                            <motion.span
                                                                key={index}
                                                                style={styles.keywordMatch}
                                                                initial={{ opacity: 0, scale: 0.8 }}
                                                                animate={{ opacity: 1, scale: 1 }}
                                                                transition={{ delay: index * 0.1 }}
                                                            >
                                                                {keyword}
                                                            </motion.span>
                                                        ))}
                                                    </div>
                                                </motion.div>

                                                {missingKeywords.length > 0 && (
                                                    <motion.div style={styles.keywordGroup} variants={itemVariants}>
                                                        <span style={styles.keywordLabel}>
                                                            ‚ùå AI-Suggested Keywords ({missingKeywords.length})
                                                        </span>
                                                        <div style={styles.keywordList}>
                                                            {missingKeywords.map((keyword, index) => (
                                                                <motion.span
                                                                    key={index}
                                                                    style={styles.keywordMissing}
                                                                    onClick={() => {
                                                                        const current = resumeData.professionalSummary;
                                                                        onInputChange('professionalSummary', 'professionalSummary',
                                                                            current ? `${current} ${keyword}` : keyword
                                                                        );
                                                                        focusTextarea();
                                                                    }}
                                                                    title="Click to add to summary"
                                                                    initial={{ opacity: 0, scale: 0.8 }}
                                                                    animate={{ opacity: 1, scale: 1 }}
                                                                    transition={{ delay: index * 0.1 }}
                                                                    whileHover={{ scale: 1.05 }}
                                                                    whileTap={{ scale: 0.95 }}
                                                                >
                                                                    {keyword}
                                                                </motion.span>
                                                            ))}
                                                        </div>
                                                    </motion.div>
                                                )}
                                            </div>
                                        </motion.div>
                                    )}
                                </AnimatePresence>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </motion.div>
            </div>

            <motion.div style={styles.buttonGroup} variants={itemVariants}>
                <motion.button
                    onClick={onPrev}
                    style={styles.secondaryButton}
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                >
                    ‚Üê Previous
                </motion.button>
                <div style={styles.buttonInfo}>
                    {resumeData.professionalSummary.length > 100 && (
                        <motion.span
                            style={styles.successText}
                            initial={{ opacity: 0, scale: 0.8 }}
                            animate={{ opacity: 1, scale: 1 }}
                        >
                            ‚úì {calculateScore() > 0 ? `AI Score: ${calculateScore()}% ‚Ä¢ ` : ''}
                            Ready for next step!
                        </motion.span>
                    )}
                </div>
                <motion.button
                    onClick={onNext}
                    style={{
                        ...styles.primaryButton,
                        ...(resumeData.professionalSummary.length < 50 && styles.primaryButtonDisabled)
                    }}
                    disabled={resumeData.professionalSummary.length < 50}
                    whileHover={resumeData.professionalSummary.length >= 50 ? { scale: 1.05 } : {}}
                    whileTap={resumeData.professionalSummary.length >= 50 ? { scale: 0.95 } : {}}
                >
                    Next: Experience ‚Üí
                </motion.button>
            </motion.div>
        </motion.div>
    );
};

