import React, { useState } from 'react';
import toast from 'react-hot-toast';

const SkillsPage = ({ resumeData, handleArrayAdd, handleArrayRemove, styles }) => {
    const [newSkill, setNewSkill] = useState({
        name: '',
        category: 'technical',
        level: 'intermediate'
    });

    const skillCategories = {
        technical: 'üíª Technical',
        soft: 'ü§ù Soft Skills',
        tools: 'üõ†Ô∏è Tools',
        languages: 'üåê Languages',
        professional: 'üíº Professional'
    };

    const handleAddSkill = () => {
        if (!newSkill.name.trim()) {
            toast.error('Please enter skill name');
            return;
        }
        handleArrayAdd('skills', newSkill);
        setNewSkill({
            name: '',
            category: 'technical',
            level: 'intermediate'
        });
        toast.success('Skill added!');
    };

    return (
        <div style={styles.stepContainer}>
            <div style={styles.stepHeader}>
                <h2 style={styles.stepTitle}>üõ†Ô∏è Skills</h2>
                <div style={styles.stepProgress}>Step 5 of 8</div>
            </div>

            <div style={styles.addSection}>
                <h3 style={styles.sectionSubtitle}>Add Skill</h3>
                <div style={styles.formGrid}>
                    <div style={styles.formGroup}>
                        <label style={styles.label}>Skill Name *</label>
                        <input
                            type="text"
                            value={newSkill.name}
                            onChange={(e) => setNewSkill(prev => ({ ...prev, name: e.target.value }))}
                            style={styles.input}
                            placeholder="e.g., JavaScript, Project Management"
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Category</label>
                        <select
                            value={newSkill.category}
                            onChange={(e) => setNewSkill(prev => ({ ...prev, category: e.target.value }))}
                            style={styles.input}
                        >
                            {Object.entries(skillCategories).map(([key, label]) => (
                                <option key={key} value={key}>{label}</option>
                            ))}
                        </select>
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Proficiency</label>
                        <select
                            value={newSkill.level}
                            onChange={(e) => setNewSkill(prev => ({ ...prev, level: e.target.value }))}
                            style={styles.input}
                        >
                            <option value="beginner">Beginner</option>
                            <option value="intermediate">Intermediate</option>
                            <option value="advanced">Advanced</option>
                        </select>
                    </div>
                </div>

                <button onClick={handleAddSkill} style={styles.primaryButton}>
                    + Add Skill
                </button>
            </div>

            <div style={styles.listSection}>
                <h3 style={styles.sectionSubtitle}>Your Skills ({resumeData.skills.length})</h3>
                {resumeData.skills.length === 0 ? (
                    <div style={styles.emptyState}>
                        <div style={styles.emptyStateIcon}>üõ†Ô∏è</div>
                        <p style={styles.emptyStateText}>No skills added yet</p>
                    </div>
                ) : (
                    <div style={styles.skillsGrid}>
                        {resumeData.skills.map((skill, index) => (
                            <div key={skill.id} style={styles.skillCard}>
                                <span style={styles.skillName}>{skill.name}</span>
                                <div style={styles.skillMeta}>
                                    <span style={styles.skillCategory}>{skillCategories[skill.category]}</span>
                                    <span style={styles.skillLevel}>{skill.level}</span>
                                </div>
                                <button
                                    onClick={() => handleArrayRemove('skills', index)}
                                    style={styles.deleteSmallButton}
                                    title="Remove skill"
                                >
                                    ‚úï
                                </button>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default SkillsPage;
