import React, { useState } from 'react';
import toast from 'react-hot-toast';

const CertificationsPage = ({ resumeData, handleArrayAdd, handleArrayRemove, styles }) => {
    const [newCert, setNewCert] = useState({
        name: '',
        issuer: '',
        date: '',
        link: '',
        description: ''
    });

    const handleAddCert = () => {
        if (!newCert.name) {
            toast.error('Certification name is required');
            return;
        }
        handleArrayAdd('certifications', newCert);
        setNewCert({
            name: '',
            issuer: '',
            date: '',
            link: '',
            description: ''
        });
        toast.success('Certification added!');
    };

    return (
        <div style={styles.stepContainer}>
            <div style={styles.stepHeader}>
                <h2 style={styles.stepTitle}>üèÜ Certifications</h2>
                <div style={styles.stepProgress}>Step 7 of 8</div>
            </div>

            <div style={styles.addSection}>
                <h3 style={styles.sectionSubtitle}>Add New Certification</h3>
                <div style={styles.formGrid}>
                    <div style={styles.formGroup}>
                        <label style={styles.label}>Certification Name *</label>
                        <input
                            type="text"
                            value={newCert.name}
                            onChange={(e) => setNewCert(prev => ({ ...prev, name: e.target.value }))}
                            style={styles.input}
                            placeholder="e.g., AWS Certified Solutions Architect"
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Issuer</label>
                        <input
                            type="text"
                            value={newCert.issuer}
                            onChange={(e) => setNewCert(prev => ({ ...prev, issuer: e.target.value }))}
                            style={styles.input}
                            placeholder="e.g., Amazon Web Services"
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Date</label>
                        <input
                            type="month"
                            value={newCert.date}
                            onChange={(e) => setNewCert(prev => ({ ...prev, date: e.target.value }))}
                            style={styles.input}
                        />
                    </div>
                </div>

                <div style={styles.formGroup}>
                    <label style={styles.label}>Description</label>
                    <textarea
                        value={newCert.description}
                        onChange={(e) => setNewCert(prev => ({ ...prev, description: e.target.value }))}
                        style={{ ...styles.input, ...styles.textareaLarge }}
                        rows={3}
                        placeholder="Description of the certification, skills learned, or relevance to your career..."
                    />
                </div>

                <div style={styles.formGroup}>
                    <label style={styles.label}>Certificate Link</label>
                    <input
                        type="url"
                        value={newCert.link}
                        onChange={(e) => setNewCert(prev => ({ ...prev, link: e.target.value }))}
                        style={styles.input}
                        placeholder="https://credential.net/your-certificate"
                    />
                </div>

                <button onClick={handleAddCert} style={styles.primaryButton}>
                    + Add Certification
                </button>
            </div>

            <div style={styles.listSection}>
                <h3 style={styles.sectionSubtitle}>Your Certifications ({resumeData.certifications.length})</h3>
                {resumeData.certifications.length === 0 ? (
                    <div style={styles.emptyState}>
                        <div style={styles.emptyStateIcon}>üèÜ</div>
                        <p style={styles.emptyStateText}>No certifications added yet</p>
                    </div>
                ) : (
                    resumeData.certifications.map((cert, index) => (
                        <div key={cert.id} style={styles.experienceCard}>
                            <div style={styles.experienceHeader}>
                                <div style={styles.experienceInfo}>
                                    <h4 style={styles.experiencePosition}>{cert.name}</h4>
                                    <p style={styles.experienceCompany}>{cert.issuer}</p>
                                    {cert.date && <p style={styles.experienceDates}>Issued: {cert.date}</p>}
                                </div>
                                <button
                                    onClick={() => handleArrayRemove('certifications', index)}
                                    style={styles.deleteButton}
                                    title="Remove certification"
                                >
                                    üóëÔ∏è
                                </button>
                            </div>
                            {cert.description && (
                                <p style={styles.experienceDescription}>{cert.description}</p>
                            )}
                            {cert.link && (
                                <a
                                    href={cert.link}
                                    style={styles.projectLink}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    üîó View Certificate
                                </a>
                            )}
                        </div>
                    ))
                )}
            </div>
        </div>
    );
};

export default CertificationsPage;
