import React, { useState } from 'react';
import toast from 'react-hot-toast';

const ProjectsPage = ({ resumeData, handleArrayAdd, handleArrayRemove, styles }) => {
    const [newProject, setNewProject] = useState({
        name: '',
        description: '',
        technologies: '',
        link: '',
        startDate: '',
        endDate: '',
        current: false
    });

    const handleAddProject = () => {
        if (!newProject.name) {
            toast.error('Project name is required');
            return;
        }
        handleArrayAdd('projects', newProject);
        setNewProject({
            name: '',
            description: '',
            technologies: '',
            link: '',
            startDate: '',
            endDate: '',
            current: false
        });
        toast.success('Project added!');
    };

    return (
        <div style={styles.stepContainer}>
            <div style={styles.stepHeader}>
                <h2 style={styles.stepTitle}>üöÄ Projects</h2>
                <div style={styles.stepProgress}>Step 6 of 8</div>
            </div>

            <div style={styles.addSection}>
                <h3 style={styles.sectionSubtitle}>Add New Project</h3>
                <div style={styles.formGrid}>
                    <div style={styles.formGroup}>
                        <label style={styles.label}>Project Name *</label>
                        <input
                            type="text"
                            value={newProject.name}
                            onChange={(e) => setNewProject(prev => ({ ...prev, name: e.target.value }))}
                            style={styles.input}
                            placeholder="Project name"
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Technologies</label>
                        <input
                            type="text"
                            value={newProject.technologies}
                            onChange={(e) => setNewProject(prev => ({ ...prev, technologies: e.target.value }))}
                            style={styles.input}
                            placeholder="React, Node.js, MongoDB..."
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>Start Date</label>
                        <input
                            type="month"
                            value={newProject.startDate}
                            onChange={(e) => setNewProject(prev => ({ ...prev, startDate: e.target.value }))}
                            style={styles.input}
                        />
                    </div>

                    <div style={styles.formGroup}>
                        <label style={styles.label}>End Date</label>
                        <input
                            type="month"
                            value={newProject.endDate}
                            onChange={(e) => setNewProject(prev => ({ ...prev, endDate: e.target.value }))}
                            style={styles.input}
                            disabled={newProject.current}
                        />
                    </div>
                </div>

                <div style={styles.formGroup}>
                    <label style={styles.checkboxLabel}>
                        <input
                            type="checkbox"
                            checked={newProject.current}
                            onChange={(e) => setNewProject(prev => ({ ...prev, current: e.target.checked, endDate: '' }))}
                        />
                        Ongoing Project
                    </label>
                </div>

                <div style={styles.formGroup}>
                    <label style={styles.label}>Project Description</label>
                    <textarea
                        value={newProject.description}
                        onChange={(e) => setNewProject(prev => ({ ...prev, description: e.target.value }))}
                        style={{ ...styles.input, ...styles.textareaLarge }}
                        rows={5}
                        placeholder="Describe the project, your role, technologies used, challenges faced, and achievements. You can write as much as you want here..."
                    />
                </div>

                <div style={styles.formGroup}>
                    <label style={styles.label}>Project Link</label>
                    <input
                        type="url"
                        value={newProject.link}
                        onChange={(e) => setNewProject(prev => ({ ...prev, link: e.target.value }))}
                        style={styles.input}
                        placeholder="https://github.com/your-project"
                    />
                </div>

                <button onClick={handleAddProject} style={styles.primaryButton}>
                    + Add Project
                </button>
            </div>

            <div style={styles.listSection}>
                <h3 style={styles.sectionSubtitle}>Your Projects ({resumeData.projects.length})</h3>
                {resumeData.projects.length === 0 ? (
                    <div style={styles.emptyState}>
                        <div style={styles.emptyStateIcon}>üöÄ</div>
                        <p style={styles.emptyStateText}>No projects added yet</p>
                    </div>
                ) : (
                    resumeData.projects.map((project, index) => (
                        <div key={project.id} style={styles.experienceCard}>
                            <div style={styles.experienceHeader}>
                                <div style={styles.experienceInfo}>
                                    <h4 style={styles.experiencePosition}>{project.name}</h4>
                                    {project.technologies && (
                                        <p style={styles.experienceCompany}>üõ†Ô∏è {project.technologies}</p>
                                    )}
                                    <div style={styles.experienceMeta}>
                                        <span style={styles.metaItem}>
                                            üìÖ {project.startDate} - {project.current ? 'Present' : project.endDate}
                                            {project.current && <span style={styles.currentBadge}>Ongoing</span>}
                                        </span>
                                    </div>
                                </div>
                                <button
                                    onClick={() => handleArrayRemove('projects', index)}
                                    style={styles.deleteButton}
                                    title="Remove project"
                                >
                                    üóëÔ∏è
                                </button>
                            </div>
                            {project.description && (
                                <p style={styles.experienceDescription}>{project.description}</p>
                            )}
                            {project.link && (
                                <a
                                    href={project.link}
                                    style={styles.projectLink}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    üîó View Project
                                </a>
                            )}
                        </div>
                    ))
                )}
            </div>
        </div>
    );
};

export default ProjectsPage;
